import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import { SessionService } from '../session.service';
import {Router} from '@angular/router'

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(private http:HttpClient,private sessionS:SessionService,private router:Router) { }
  whoIs=""
  hide=true
  ngOnInit(): void {
    var userData=this.sessionS.getUser()
    this.whoIs=userData.user.name
    console.log("welcome: Dash board intialisation")
    console.log(userData)
    if(userData.user.designation=="Head"){
      this.hide=false

    }
  }
  dashboardCall(){
    this.router.navigateByUrl("")
  }
  logout(){
    var user=this.sessionS.getUser();
    this.http.post("/auth/logout",{token:user.token}).subscribe(
      (data)=>{
        this.sessionS.deleteUser()
        this.router.navigateByUrl("/login")
      },
      (err)=>{}
    )
  }
  getAdminPage(){
    this.router.navigateByUrl("/AdminOp1")
  }

}
