import { Component, OnInit } from '@angular/core';
import {SessionService} from '../session.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
@Component({
  selector: 'app-admin-booking',
  templateUrl: './admin-booking.component.html',
  styleUrls: ['./admin-booking.component.css']
})
export class AdminBookingComponent implements OnInit {
  showTableUser=false;
  allUsers:any

  constructor(private sessionS:SessionService,private http:HttpClient,private router:Router) { }

  ngOnInit(): void {
  }
  getUsers(){
    this.showTableUser=true;
    var user=this.sessionS.getUser()
    this.http.get(`/Admin/slot?token=${user.token}`).subscribe(
      data=>{
        console.log(data);
        this.allUsers=data
        this.allUsers.sort(function(a, b) {
          return a.date-b.date;
      });
        for(const element of this.allUsers){
          console.log(element)
          element.date=new Date(element.date);
        }
        
      },
      error=>{
        console.log(error)
        if(error.status==401){
          this.sessionS.deleteUser();
          this.router.navigateByUrl("/login")
        }
      }
    )
  }
  showTable(){
    this.showTableUser=false;
  }

}
