import { Component, OnInit } from '@angular/core';
import { DateSetting1Service } from '../date-setting1.service';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {
  constructor(private dateSelect:DateSetting1Service) { }

  ngOnInit(): void {
  }
  productsArray=[
    {name:"flowers",price:20.00},
    {name:"coffe",price:40.00},
    {name:"staffservice",price:60.00}
  ]
  productObj={
    flowers:0,
    coffe:0,
    staffservice:0
  }
  grandTotal=0;
  selectedMonth=this.dateSelect.getDate().month
  selectedDay=this.dateSelect.getDate().day
  nDateString=""
  total(){
    console.log(this.selectedMonth)
    console.log(this.dateSelect.getDate().day)
    var millis=this.dateSelect.getDate().milli
    var d = new Date(millis);
    this.nDateString = d.toDateString()
    console.log(this.nDateString)
    var totalPrice=0
    for(var item of this.productsArray){
      totalPrice=this.productObj[item.name]*item.price+totalPrice
    }
    this.grandTotal=totalPrice    
  }


}

