import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DateSetting1Service {

  constructor() { }  
  getDate(){
    var userDate=localStorage.getItem("date_info")
    return userDate!=null? JSON.parse(userDate):{}
  }
  setDate(userDate){
    localStorage.setItem("date_info",JSON.stringify(userDate))
  }

}
