import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import { SessionService } from '../session.service';
import {Router} from '@angular/router'

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css']
})
export class LogoutComponent implements OnInit {

  constructor(private http:HttpClient,private sessionS:SessionService,private router:Router) { }

  ngOnInit(): void {
  }
  logout(){
    var user=this.sessionS.getUser();
    this.http.post("/auth/logout",{token:user.token}).subscribe(
      (data)=>{
        this.sessionS.deleteUser()
        this.router.navigateByUrl("/login")
      },
      (err)=>{}
    )
  }
  getAdminPage(){
    this.router.navigateByUrl("/AdminOp1")
  }

}
