import { Injectable } from '@angular/core';
//import { userInfo } from 'os';

@Injectable({
  providedIn: 'root'
})
export class TimeSetService {

  constructor() { }
  getTime(){
    var userTime=localStorage.getItem("time_info")
    return userTime!=null? JSON.parse(userTime):{}
  }
  setTime(userTime){
    localStorage.setItem("time_info",JSON.stringify(userTime))
  }
}
